@import url(https://fonts.googleapis.com/css2?family=Cinzel&family=Roboto&display=swap);
.header {
  display: flex;
  justify-content: center;
  padding: 1.5rem 0;
  background: rgb(236, 233, 233);
}

.title {
  font-size: 2rem;
  font-family: 'Cinzel', serif !important;
  font-weight: 500;
}

.input-container {
  display: flex;
  flex-direction: column;
}

.input-label {
  font-size: 1rem;
  padding-left: 0.25rem;
  padding-bottom: 0.25rem;
  color: rgb(77, 77, 77);
}

.input-box {
  font-size: 1.5rem;
  border: none;
  border-bottom: 1px solid rgba(177, 177, 177, 0.795);
}

.input-textarea {
  width: 100%;
  height: 100px;
  font-size: 1.5rem;
  border: none;
  border-bottom: 1px solid rgba(177, 177, 177, 0.795);
}

.input-box:focus,
.input-textarea:focus {
  outline: none;
  border-bottom: 1px solid black;
}

.end-div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 0.25rem;
  padding-bottom: 0.25rem;
}

.end-div > .input-label {
  padding: 0;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type='number'] {
  -moz-appearance: textfield;
}

.btn {
  margin: auto;
  display: flex;
  place-items: center;
  color: white;
  padding: 0.5rem 0.5rem;
  border-radius: 100%;
  border: none;
  cursor: pointer;
}

.btn:focus {
  outline: none;
}

.btn:hover {
  box-shadow: 0 0 10px black;
}

.add-btn {
  background: rgba(0, 132, 255, 0.582);
}

.delete-btn {
  background: rgba(255, 26, 26, 0.671);
}

.extradiv {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 2rem 3rem;
  gap: 2rem 3rem;
}

.skills-div {
  display: flex;
  justify-content: space-between;
}

.skills-div > .btn {
  margin-left: 1rem;
}

.skills-btn {
  font-size: 1rem;
  border-radius: 3rem;
  padding: 1rem 1.5rem;
}

.body-info {
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.section-title {
  font-size: 2rem;
  color: white;
  font-family: 'Roboto', sans-serif;
}

.buttons {
  padding-top: 1rem;
  display: flex;
  justify-content: space-between;
  padding-bottom: 3rem;
}

.buttons > button {
  padding: 1rem 1.5rem;
  font-size: 1rem;
  border-radius: 0.5rem;
  background: white;
  margin: 0 0.5rem;
  cursor: pointer;
  border: none;
}

.buttons > button:focus {
  outline: none;
}

.buttons > button:hover {
  box-shadow: 0 0 10px black;
}

.start-btn {
  margin-top: 13rem;
  font-size: 2rem;
  padding: 1.5rem 2rem;
  border-radius: 3rem;
  background: rgb(236, 233, 233);
  border: none;
  cursor: pointer;
}

.start-btn:focus {
  outline: none;
}

.start-btn:hover {
  background: white;
  box-shadow: 0 0 10px black;
}

.preview {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
}

.preview > .buttons {
  padding-bottom: 1rem;
}

h1,
label,
input,
textarea,
div,
button {
  font-family: 'Roboto', sans-serif !important;
}

html,
body,
#root,
.App {
  height: 100%;
}

body {
  background: rgba(0, 132, 255, 0.582);
}

.section-container {
  margin-top: 1rem;
  background: white;
  padding: 1.5rem 2.5rem;
  display: grid;
  border-radius: 1rem;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.425);
  grid-template-columns: 1fr 1fr;
  grid-gap: 2rem 3rem;
  gap: 2rem 3rem;
}

.two-cols {
  grid-column: 1 / 3;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

